import React, { useState } from "react";

const RevokeForm = ({ purchase, customer_id, cancelPurchase }) => {
  const [reason, setReason] = useState("");
  return (
    <div>
      <p>
        Ests seguro que deseas revocar el acceso de la compra {purchase.title}
        ? Esta accin NO puede deshacerse.
      </p>
      <label>Razn</label>
      <input
        type="text"
        value={reason}
        className="form-control mb-3"
        onChange={(e) => setReason(e.target.value)}
      />
      <button
        className="btn btn-danger"
        onClick={() =>
          cancelPurchase(purchase.purchase_id, reason, customer_id)
        }
      >
        Revocar Acceso
      </button>
    </div>
  );
};

export default RevokeForm;
